import styled from "styled-components"
import ReactTooltip from "react-tooltip"
import { prettyGoodId } from "../../util/misc"

const StyledInstructions = styled.div`
  background-color: antiquewhite;
  margin: -3px -16px;
  padding: 5px;
  border-radius: 2px;
  text-shadow: none;
  color: black;
`

const Instructions = ({ children }) => (
  <StyledInstructions>{children}</StyledInstructions>
)

const StyledInformationButton = styled.button`
  display: inline-block;
  border-radius: 50%;
  border: 3px solid cornflowerblue;
  color: cornflowerblue;
  height: 100%;
  width: auto;
  cursor: help;
  margin: 10px;
  padding: 0 0.45em;
  font-size: 0.5em;
  outline: none;
`

const InformationTooltip = ({ children, triggerText = "i", triggerStyle }) => {
  const id = prettyGoodId("tooltip")
  return (
    <>
      <ReactTooltip
        id={id}
        getContent={() => <Instructions>{children}</Instructions>}
        effect="solid"
        place="bottom"
      />
      <StyledInformationButton data-for={id} data-tip style={triggerStyle}>
        {triggerText}
      </StyledInformationButton>
    </>
  )
}

export default InformationTooltip
