import { useState, useEffect } from "react"
import styled from "styled-components"
import { Centered, RangeSlider } from "./game/Util"
import { gameData } from "../util/data"

const toNoiseImage = (image, index) => ({
  index,
  src: `img/${image.path}`,
  label: image.label,
})

const Container = styled.div`
  display: flex;
  flex-direction: column;
  background-color: whitesmoke;
  padding: 10px;
  border: 1px solid gray;
  border-radius: 5px;
`

const StyledImageTile = styled.div`
  position: relative;
  display: inline-block;
  height: ${({ size = {} }) => size.height || "180px"};
  width: ${({ size = {} }) => size.width || "180px"};
  margin: 2px;
`

const StyledImageTileImage = styled.img`
  height: 100%;
  width: 100%;
  border-radius: ${({ rounded }) => (rounded ? "5px" : "none")};
`

const StyledImageLabel = styled.div`
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 5px;
  font-size: 75%;
  color: white;
  background: rgba(0, 0, 0, 0.5);
  border-top-left-radius: 3px;
  border-bottom-right-radius: ${({ rounded }) => (rounded ? "5px" : "none")};
`

const ImageTile = ({ image, size, rounded }) => (
  <StyledImageTile size={size}>
    <StyledImageLabel title={image.title} rounded={rounded}>
      {image.label}
    </StyledImageLabel>
    <StyledImageTileImage src={image.src} alt="" rounded={rounded} />
  </StyledImageTile>
)

const ImageTiles = ({ images, tileSize, rounded }) => (
  <div>
    {images.map((x, i) => (
      <ImageTile
        key={`image-tile-${x}-${i}`}
        image={x}
        size={tileSize}
        rounded={rounded}
      />
    ))}
  </div>
)

const ImageNoise = ({ imageSetNumber = 3, rounded = true }) => {
  const [rawImages, setRawImages] = useState([])
  const [images, setImages] = useState([])
  const [noiseLevel, setNoiseLevel] = useState(0)
  const [noiseLevelRange, setNoiseLevelRange] = useState([0, 0])
  useEffect(() => {
    const getImageData = async () => {
      const _gameData = await gameData()
      const _rawImages = _gameData.practiceOptions[imageSetNumber]
      setRawImages(_rawImages)
      const noiseLevels = [
        ...new Set(
          _rawImages.reduce(
            (all, x) => [
              ...all,
              ...Object.keys(x.variants || {})
                .filter((x) => x.startsWith("noise"))
                .map((x) => parseInt(x.replace("noise", ""))),
            ],
            []
          )
        ),
      ]
      setNoiseLevelRange([0, Math.max(...noiseLevels)])
      setImages(_rawImages.map(toNoiseImage))
    }

    getImageData() // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    if (noiseLevel === 0) return
    const noisyImages = rawImages.map(
      (x, i) => toNoiseImage(x.variants[`noise${noiseLevel}`], i) // TODO What if there is no noise level for this image?
    )
    setImages(noisyImages) // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [noiseLevel])

  const [sliderMin, sliderMax] = noiseLevelRange
  return images && images.length ? (
    <Centered>
      <Container>
        <ImageTiles
          images={images}
          tileSize={{ height: "180px", width: "180px" }}
          rounded={rounded}
        />
        {sliderMax > 0 ? (
          <div style={{ display: "flex", justifyContent: "center" }}>
            <RangeSlider
              label="Add Noise"
              labelPosition="bottom"
              min={sliderMin}
              max={sliderMax}
              step={1}
              value={noiseLevel}
              style={{
                outline: "none",
                width: "100%",
              }}
              onChange={setNoiseLevel}
            />
          </div>
        ) : null}
      </Container>
    </Centered>
  ) : null
}

export default ImageNoise
