import { useState, useEffect } from "react"
import { chain } from "../../util/misc"
import styled from "styled-components"
import Xarrow from "react-xarrows"
// import { usePrevious } from "../../hooks"

const RowFlexContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: ${({ valign = "center" }) => valign};
`

const defaultLinkConfig = {
  size: 4,
  head: 3,
  color: "coral",
}

const FlowCharts = {
  validate: (elements) => {
    const issues = []
    elements.forEach((x) => {
      if (!x.id && !x.props.id)
        issues.push(`FlowChart element [${x.type}] is missing id`)
    })
    return issues
  },
  hasPosition: (x) =>
    x.style &&
    ("top" in x.style ||
      "bottom" in x.style ||
      "left" in x.style ||
      "right" in x.style),
  hasPositionedElements: (elements) =>
    elements.reduce((all, x) => all || FlowCharts.hasPosition(x), false),
  hasUserDefinedLinks: (elements) =>
    elements.reduce((all, x) => all || "links" in x, false),
  buildLinks: (elements) => {
    const userDefined = FlowCharts.hasUserDefinedLinks(elements)
    if (userDefined) {
      return elements
        .map((x) =>
          (Object.keys(x.links || {}) || []).map((l) => ({
            from: x.id,
            to: l,
            config: { ...defaultLinkConfig, ...x.links[l] },
          }))
        )
        .flat()
    } else {
      // Build links from element to element in order given
      return elements.reduce((all, x, i) => {
        if (i === elements.length - 1) return all
        return [
          ...all,
          {
            from: x.props.id,
            to: elements[i + 1].props.id,
            config: defaultLinkConfig,
          },
        ]
      }, [])
    }
  },
}

const StyledFlowChartElement = styled.div`
  display: flex;
  justify-content: space-around;
  & * {
    display: block;
  }
`

const FlowChart = ({
  name,
  elements,
  size,
  style,
  startAnimating,
  visible,
  chainLinkAnimation,
  betweenLinkDelay = 0,
}) => {
  const shouldAnimate = visible && startAnimating
  const [links, setLinks] = useState([])
  const issues = FlowCharts.validate(elements)
  if (issues.length) {
    console.error(issues.join("\n"))
  }

  const linkId = (x) => `${x.from}-${x.to}`
  useEffect(() => {
    if (shouldAnimate) {
      if (chainLinkAnimation) {
        setLinks((prev) => [])
        const fns = FlowCharts.buildLinks(elements).map((x) => () =>
          setLinks((prev) =>
            prev.find((l) => linkId(l) === linkId(x)) ? prev : [...prev, x]
          )
        )
        chain(fns, betweenLinkDelay)
      } else {
        setLinks(FlowCharts.buildLinks(elements))
      }
    } else if (!visible) {
      setLinks([])
    }
  }, [
    setLinks,
    elements,
    shouldAnimate,
    visible,
    betweenLinkDelay,
    chainLinkAnimation,
  ])

  const containerStyle = FlowCharts.hasPositionedElements(elements)
    ? { position: "relative", ...size }
    : {
        display: "flex",
        justifyContent: "space-evenly",
        alignItems: "center",
        flex: 1,
      }

  const elementStyle = (x) =>
    FlowCharts.hasPosition(x) ? { position: "absolute", ...x.style } : null

  return (
    <RowFlexContainer style={style}>
      <div style={containerStyle}>
        {elements.map((x, i) => (
          <StyledFlowChartElement
            key={`${name}-${i}`}
            id={x.id}
            style={elementStyle(x)}
          >
            {x.element ? x.element : x}
          </StyledFlowChartElement>
        ))}
        {links.map((x) => {
          return (
            <Xarrow
              key={`${name}-line-${x.from}-${x.to}`}
              start={`${x.from}`}
              end={`${x.to}`}
              color={x.config.color}
              path={x.config.path}
              strokeWidth={x.config.size}
              startAnchor={x.config.startSocket}
              endAnchor={x.config.endSocket}
              headSize={x.config.head}
              animateDrawing={shouldAnimate ? "0.5s" : false}
            />
          )
        })}
      </div>
    </RowFlexContainer>
  )
}

export default FlowChart
