export const clean = (obj) =>
  Object.keys(obj)
    .filter((k) => obj[k] != null)
    .reduce((a, k) => ({ ...a, [k]: obj[k] }), {})

export const styleDiv = (getDiv, style) => {
  const div = getDiv()
  Object.keys(style).forEach((k) => (div.style[k] = style[k]))
}

export const isDef = (x) => x !== null && x !== undefined

// https://stackoverflow.com/a/56749849/1072068
export const shuffle = (arr) =>
  [...arr].reduceRight(
    (
      res,
      _,
      __,
      s // eslint-disable-next-line no-sequences
    ) => (res.push(s.splice(0 | (Math.random() * s.length), 1)[0]), res),
    []
  )

export const print = console.log.bind(console)

export const isObject = (x) => typeof x === "object" && x !== null

export const chain = async (fns, duration) => {
  const delay = (t) =>
    new Promise((resolve) => {
      setTimeout(() => {
        resolve()
      }, t)
    })

  for (let x of fns) {
    x()
    await delay(duration || 0)
  }
}

export const prettyGoodId = (prefix) =>
  `${prefix}${Date.now()}${Math.floor(Math.random() * 100)}`
