import { useEffect, useState } from "react"
import ThresholdChartSet, { ChartData } from "./ThresholdChartSet"

const Colors = {
  red: "#FF4136",
  blue: "#0074D9",
  lightgray: "lightgray",
  gray: "gray",
}

const LegendModel = {
  positiveAboveThreshold: { color: Colors.blue, text: "IN - Above Threshold" },
  positiveBelowThreshold: { color: Colors.gray, text: "IN - Below Threshold" },
  negativeAboveThreshold: { color: Colors.red, text: "OUT - Above Threshold" },
  negativeBelowThreshold: {
    color: Colors.lightgray,
    text: "OUT - Below Threshold",
  },
}

const ChartModels = {
  dotPlot: {
    groups: [
      {
        label: "In Distances",
        fn: (x) => x.label.includes("_train_"),
        color: Colors.blue,
      },
      {
        label: "Out Distances",
        fn: (x) => x.label.includes("_out_"),
        color: Colors.red,
      },
    ],
  },
  correct: {
    title: "Correct",
    groups: [
      {
        label: "Incorrect",
        fn: (x, t) => x.errorRounded < t && x.path.includes("_out_"),
        color: Colors.lightgray,
      },
      {
        label: "Correct",
        fn: (x, t) => x.errorRounded >= t && x.path.includes("_train_"),
        color: Colors.blue,
      },
    ],
  },
  incorrect: {
    title: "Incorrect",
    groups: [
      {
        label: "Incorrect",
        fn: (x, t) => x.errorRounded >= t && x.path.includes("_out_"),
        color: Colors.red,
      },
      {
        label: "Correct",
        fn: (x, t) => x.errorRounded < t && x.path.includes("_train_"),
        color: Colors.gray,
      },
    ],
  },
  truePositive: {
    title: "True Positive Rate",
    groups: [
      {
        label: "In",
        fn: (x, t) => x.errorRounded >= t && x.path.includes("_train_"),
        color: Colors.blue,
      },
      {
        label: "In Below Threshold",
        fn: (x, t) => x.errorRounded < t && x.path.includes("_train_"),
        color: Colors.gray,
      },
    ],
  },
  positive: {
    title: "Positive Rate",
    groups: [
      {
        label: "In",
        fn: (x, t) => x.errorRounded >= t && x.path.includes("_train_"),
        color: Colors.blue,
      },
      {
        label: "In Below Threshold",
        fn: (x, t) => x.errorRounded < t && x.path.includes("_train_"),
        color: Colors.gray,
      },
      {
        label: "Out",
        fn: (x, t) => x.errorRounded >= t && x.path.includes("_out_"),
        color: Colors.red,
      },
      {
        label: "Out Below Threshold",
        fn: (x, t) => x.errorRounded < t && x.path.includes("_out_"),
        color: Colors.lightgray,
      },
    ],
  },
}
const ExperimentThresholdChartSet = ({
  nodeSize = 10,
  defaultThreshold = 4.5,
  height = 300,
  width = 600,
  thresholdPercent,
  bottomLabel,
  sliderLabel,
}) => {
  const [rawData, setRawData] = useState([])
  const [models, setModels] = useState({
    dotPlot: { data: [] },
    correct: { data: [], total: 0 },
    incorrect: { data: [], total: 0 },
    truePositive: { data: [], total: 0 },
    positive: { data: [], total: 0 },
  })
  const [dotPlotMaxX, setDotPlotMaxX] = useState(0)
  const [threshold, setThreshold] = useState(defaultThreshold)

  useEffect(() => {
    const getRawData = async () => {
      const result = await fetch("./data/dot-plot-results.json")
      const data = await result.json()
      setRawData(
        data.results.map((x) => ({
          ...x,
          errorRounded: ChartData.closestX(x.error),
        }))
      )
    }

    getRawData()
  }, [])

  useEffect(() => {
    const _dotPlotData = ChartData.dotPlot(rawData, ChartModels.dotPlot)
    setModels({
      dotPlot: _dotPlotData,
      correct: ChartData.rollup(rawData, ChartModels.correct, defaultThreshold),
      incorrect: ChartData.rollup(
        rawData,
        ChartModels.incorrect,
        defaultThreshold
      ),
      truePositive: ChartData.rollup(
        rawData,
        ChartModels.truePositive,
        defaultThreshold
      ),
      positive: ChartData.rollup(
        rawData,
        ChartModels.positive,
        defaultThreshold
      ),
    })

    setThreshold(defaultThreshold)
    if (_dotPlotData.maxX) setDotPlotMaxX(_dotPlotData.maxX + 1)
  }, [rawData, defaultThreshold])

  useEffect(() => {
    let _threshold = threshold
    if (thresholdPercent) {
      _threshold = ChartData.closestX(dotPlotMaxX * thresholdPercent)
      setThreshold(_threshold)
    }
    setModels({
      ...models,
      correct: ChartData.rollup(rawData, ChartModels.correct, _threshold),
      incorrect: ChartData.rollup(rawData, ChartModels.incorrect, _threshold),
      truePositive: ChartData.rollup(
        rawData,
        ChartModels.truePositive,
        _threshold
      ),
      positive: ChartData.rollup(rawData, ChartModels.positive, _threshold),
    }) // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [threshold, thresholdPercent])
  return (
    <ThresholdChartSet
      nodeSize={nodeSize}
      height={height}
      width={width}
      threshold={threshold}
      models={models}
      legendModel={LegendModel}
      dotPlotMaxX={dotPlotMaxX}
      bottomLabel={bottomLabel}
      sliderLabel={sliderLabel}
      setThreshold={setThreshold}
      dotPlotColorFn={(x) =>
        x.x < threshold
          ? x.label.includes("out")
            ? "lightgray"
            : "gray"
          : x.label && x.label.includes("out")
          ? Colors.red
          : Colors.blue
      }
    />
  )
}

export default ExperimentThresholdChartSet
