import styled from "styled-components"
import { ResponsiveScatterPlot } from "@nivo/scatterplot"
import { ResponsiveWaffle } from "@nivo/waffle"
import { ResponsivePie } from "@nivo/pie"
import { isDef } from "../../util/misc"
import { RangeSlider } from "../game/Util"

export const ChartData = {
  closestX: (x) => Math.round(x * 2) / 2,
  dotPlot: (results, { groups }) => {
    const filtered = (data, filterFn) =>
      Object.keys(data)
        .map((x) => data[x].map((y, i) => ({ x: x, y: i + 1, label: y })))
        .flat()
        .filter(filterFn)

    const binned = results.reduce(
      (all, x) => ({
        ...all,
        [x.errorRounded]: [...(all[x.errorRounded] || []), x.path],
      }),
      {}
    )
    return {
      groups,
      colors: groups.map((x) => x.color),
      maxX: Object.keys(binned).length ? Math.max(...Object.keys(binned)) : 0,
      data: groups.map((x) => ({
        id: x.label,
        data: filtered(binned, x.fn),
      })),
    }
  },
  rollup: (results, { title, groups }, threshold) => {
    const starterBin = groups.reduce((all, x) => ({ ...all, [x.label]: 0 }), {})
    const binned = results.reduce(
      (binned, x) =>
        (groups || []).reduce(
          (all, g) =>
            g.fn(x, threshold)
              ? { ...all, [g.label]: (all[g.label] || 0) + 1 }
              : { ...all },
          binned
        ),
      starterBin
    )
    return {
      groups,
      title,
      colors: groups.map((x) => x.color),
      data: groups.map((g) => ({
        id: g.label,
        label: g.label,
        value: binned[g.label],
      })),
      total: results.length,
    }
  },
}

const Tooltip = styled.div`
  background-color: white;
  border-radius: 3px;
  padding: 5px;
  border: 1px solid gray;
`

const ChartContainer = styled.div`
  height: ${({ height = 150 }) => `${height}px`};
  width: ${({ width = 150 }) => `${width}px`};
  background-color: white;
  border: 1px solid lightgray;
  border-radius: 5px;
  margin: 5px;
`

const reducePropWithFn = (fn, propKey, data) =>
  data && data.length
    ? data.reduce(
        (cumulative, v) => fn(...v.data.map((d) => d[propKey]), cumulative),
        0
      )
    : 0

const Thresholds = {
  maxXValue: reducePropWithFn.bind(null, Math.max, "x"),
  maxYValue: reducePropWithFn.bind(null, Math.max, "y"),
  minXValue: reducePropWithFn.bind(null, Math.min, "x"),
  minYValue: reducePropWithFn.bind(null, Math.min, "y"),
  yCount: (data, x) =>
    data && data.length
      ? data.reduce(
          (count, set) =>
            set.data.filter((item) => item.x.toString() === x.toString())
              .length + count,
          0
        )
      : 0,
}
const Threshold = ({ data, threshold, xScale, yScale, nodeSize }) => {
  const beyondYBuffer = 5
  if (!isDef(data) || !isDef(threshold) || !isDef(nodeSize)) return null

  const [columnX1, columnY1, columnX2, columnY2] = [
    xScale(threshold),
    yScale(-beyondYBuffer),
    xScale(threshold),
    yScale(Thresholds.maxYValue(data) + beyondYBuffer),
  ]

  const columnCount = Thresholds.yCount(data, threshold)

  const minX = Thresholds.minXValue(data)
  const maxX = Thresholds.maxXValue(data)
  const minXEdge = minX - 1
  const maxXEdge = maxX + 2
  const linePadding = nodeSize / 2.0 + 1
  const columnPolyPoints = [
    `${columnX1 - linePadding},${columnY1}`,
    `${columnX1 - linePadding},${columnY2}`,
    `${columnX2 + linePadding},${columnY2}`,
    `${columnX2 + linePadding},${columnY1}`,
  ]
  // const inAreaPolyPoints = [
  //   `${columnX2 + linePadding},${columnY2}`,
  //   `${columnX2 + linePadding},${columnY1}`,
  //   `${xScale(maxX)},${columnY1}`,
  //   `${xScale(maxX)},${columnY2}`,
  // ]
  const outAreaPolyPoints = [
    `${xScale(minXEdge)},${columnY1}`,
    `${xScale(minXEdge)},${columnY2}`,
    `${columnX1 - linePadding},${columnY2}`,
    `${columnX1 - linePadding},${columnY1}`,
  ]
  const inLabelX = columnX2 + (xScale(maxXEdge) - columnX2) / 2.0
  const outLabelX = columnX1 - (columnX1 - xScale(minXEdge)) / 2.0
  return isNaN(columnX1) ||
    isNaN(columnX2) ||
    isNaN(columnY1) ||
    isNaN(columnY2) ? null : (
    <>
      <>
        {threshold > minX ? (
          <text
            x={outLabelX}
            y={-10}
            textAnchor="middle"
            style={{ fontSize: ".7rem" }}
          >
            &#8592; OUT
          </text>
        ) : null}
        {columnCount ? (
          <text
            x={columnX1}
            y={-10}
            textAnchor="middle"
            style={{ fontSize: ".63rem" }}
          >
            {columnCount}
          </text>
        ) : null}
        {threshold <= maxX ? (
          <text
            x={inLabelX}
            y={-10}
            textAnchor="middle"
            style={{ fontSize: ".7rem" }}
          >
            IN &#8594;
          </text>
        ) : null}
      </>
      <polygon
        points={columnPolyPoints.join(" ")}
        fill="rgba(0,0,0,0)"
        stroke="rgba(20,20,20,1)"
        strokeWidth="1.5"
      />
      <polygon points={outAreaPolyPoints.join(" ")} fill="rgba(0,0,0,.15)" />
    </>
  )
}

const DotPlot = ({
  model,
  nodeSize = 13,
  threshold,
  maxX = "auto",
  minX = "auto",
  colorFn,
  bottomLabel,
}) => (
  <ResponsiveScatterPlot
    data={model.data}
    margin={{ top: 20, bottom: 50, right: 20, left: 20 }}
    xScale={{ type: "linear", min: minX, max: maxX }}
    colors={colorFn || model.colors}
    tooltip={(x) => <Tooltip>{x.node.data.label}</Tooltip>}
    nodeSize={nodeSize}
    axisBottom={{
      orient: "bottom",
      legend: bottomLabel,
      legendPosition: "middle",
      legendOffset: 35,
    }}
    axisLeft={false}
    threshold={threshold}
    layers={["axes", "nodes", Threshold]}
    legends={[
      {
        anchor: "top-right",
        direction: "column",
        translateX: 110,
        translateY: 0,
        itemWidth: 90,
        itemHeight: 12,
        itemsSpacing: 5,
        symbolSize: nodeSize,
        symbolShape: "circle",
        effects: [
          {
            on: "hover",
            style: {
              itemTextColor: "#000",
              itemBackground: "#f7fafb",
            },
          },
        ],
      },
    ]}
  />
)

const StyledChartTitle = styled.div`
  font-weight: bold;
  font-size: 0.8rem;
  text-align: center;
  margin: 5px 0;
`
const WaffleDot = ({
  size,
  x,
  y,
  color,
  fill,
  opacity,
  borderWidth,
  borderColor,
  onHover,
  onLeave,
}) => (
  <circle
    r={size / 2}
    cx={x + size / 2}
    cy={y + size / 2}
    fill={fill || color}
    strokeWidth={borderWidth}
    stroke={borderColor}
    opacity={opacity}
    onMouseEnter={onHover}
    onMouseMove={onHover}
    onMouseLeave={onLeave}
  />
)
const Waffle = ({ model }) => {
  if (!model.total) return null
  return (
    <>
      {model.title ? <StyledChartTitle>{model.title}</StyledChartTitle> : null}
      <ResponsiveWaffle
        data={model.data}
        total={model.total}
        rows={model.total / 10 + 1}
        columns={10}
        colors={model.colors}
        margin={{ top: 0 }}
        cellComponent={WaffleDot}
        fillDirection="top"
        emptyColor="rgba(0,0,0,0)"
      />
    </>
  )
}

const PieCenterTotal = ({ dataWithArc, centerX, centerY }) => {
  const total = dataWithArc.reduce((all, x) => all + x.value, 0)
  return (
    <text
      x={centerX}
      y={centerY}
      textAnchor="middle"
      dominantBaseline="central"
      style={{
        fontSize: "1.5rem",
      }}
    >
      {total}
    </text>
  )
}

const Pie = ({ model, showTotal = true }) => {
  return (
    <>
      {model.title ? <StyledChartTitle>{model.title}</StyledChartTitle> : null}
      <ResponsivePie
        data={model.data}
        margin={{ top: 35, right: 120, bottom: 80, left: 120 }}
        colors={model.colors}
        innerRadius={0.6}
        enableRadialLabels={false}
        padAngle={0.5}
        cornerRadius={2}
        layers={[
          "slices",
          "sliceLabels",
          "radialLabels",
          "legends",
          showTotal ? PieCenterTotal : null,
        ]}
        sliceLabel={(x) => (x.value ? x.value : null)}
        sliceLabelsTextColor="white"
      />
    </>
  )
}

const LegendPart = ({ color, text, colorPosition = "left" }) => {
  const swatch = (
    <div
      style={{
        width: 14,
        height: 14,
        borderRadius: "50%",
        backgroundColor: color,
        margin: "0 5px",
      }}
    />
  )
  return (
    <div
      style={{
        display: "flex",
        textAlign: colorPosition,
        justifyContent: colorPosition === "left" ? "flex-start" : "flex-end",
        padding: "1px 0",
      }}
    >
      {colorPosition === "left" ? swatch : null}
      <span style={{ fontSize: ".6rem" }}>{text}</span>
      {colorPosition === "right" ? swatch : null}
    </div>
  )
}

const Legend = ({ model }) => (
  <div
    style={{
      display: "grid",
      gridTemplateColumns: "50% 50%",
    }}
  >
    <LegendPart colorPosition="right" {...model.negativeBelowThreshold} />
    <LegendPart {...model.positiveBelowThreshold} />
    <LegendPart colorPosition="right" {...model.negativeAboveThreshold} />
    <LegendPart {...model.positiveAboveThreshold} />
  </div>
)

const FlexDiv = styled.div`
  display: flex;
`
const FlexColumns = styled(FlexDiv)`
  flex-direction: column;
`
const FlexRows = styled(FlexDiv)`
  flex-direction: row;
`
const ThresholdChartSet = ({
  nodeSize = 10,
  height = 300,
  width = 600,
  threshold,
  models,
  dotPlotMaxX,
  bottomLabel,
  sliderLabel,
  setThreshold,
  dotPlotColorFn,
  legendModel,
}) => {
  const dotPlotWidth = width / 2
  const smallChartHeight = height / 2 - 6
  return (
    <FlexDiv>
      {dotPlotMaxX ? (
        <FlexColumns>
          <ChartContainer height={height} width={dotPlotWidth}>
            <DotPlot
              model={models.dotPlot}
              nodeSize={nodeSize}
              threshold={threshold}
              minX={0}
              maxX={dotPlotMaxX}
              colorFn={dotPlotColorFn}
              bottomLabel={bottomLabel}
              legendModel={legendModel}
            />
          </ChartContainer>
          <RangeSlider
            label={sliderLabel}
            labelPosition="bottom"
            min={0}
            max={dotPlotMaxX}
            step={0.5}
            value={threshold}
            style={{
              outline: "none",
              width: dotPlotWidth - 40,
              marginLeft: 26,
              marginTop: 4,
            }}
            handleStyle={{
              padding: 3,
              borderRadius: "0 0 50% 50%",
            }}
            onChange={(level) => setThreshold(level)}
          />
        </FlexColumns>
      ) : null}
      <FlexColumns>
        <FlexRows>
          <ChartContainer height={smallChartHeight}>
            <Waffle model={models.correct} />
          </ChartContainer>
          <ChartContainer height={smallChartHeight}>
            <Waffle model={models.incorrect} />
          </ChartContainer>
        </FlexRows>
        <FlexRows>
          <ChartContainer height={smallChartHeight}>
            <Pie model={models.truePositive} />
          </ChartContainer>
          <ChartContainer height={smallChartHeight}>
            <Pie model={models.positive} />
          </ChartContainer>
        </FlexRows>
        <Legend model={legendModel} />
      </FlexColumns>
    </FlexDiv>
  )
}

