import { GameContextProvider } from "../context/GameContext"
import { useState } from "react"
import { withRouter } from "react-router-dom"
import CheckButton from "react-grid-gallery/lib/CheckButton"
import { Fab, Action } from "react-tiny-fab"
import "react-tiny-fab/dist/styles.css"
import TwentyQuestions from "../components/game/TwentyQuestions"
import Match from "../components/game/Match"
import TwentyQuestionsPractice from "../components/game/TwentyQuestionsPractice"
import Menu from "../components/game/Menu"
import Story from "../components/Story"
import { Emoji } from "../components/game/Util"
import Modal from "react-modal"
import { HashRouter as Router, Switch, Route } from "react-router-dom"

Modal.setAppElement("#root")

const PageHeader = ({ title, subtitle }) => {
  const styles = {
    container: {
      marginBottom: 5,
    },
    title: {
      fontSize: 20,
      fontWeight: "bold",
    },
    subtitle: {
      fontSize: 14,
      color: "gray",
    },
  }
  return (
    <div style={styles.container}>
      <div style={styles.title}>{title}</div>
      <span style={styles.subtitle}>{subtitle}</span>
    </div>
  )
}

const GameModal = withRouter(({ history }) => {
  const [isModalOpen, setModalOpen] = useState(false)
  const toggleModal = () => setModalOpen(!isModalOpen)
  const returnToMenu = () => {
    if (window.location.hash === "#/") {
      // Already on the main page, scroll up to the menu
      window.scrollTo({ top: 0, left: 0, behavior: "auto" })
    } else {
      history.push("/")
    }
  }

  return (
    <>
      <Modal
        isOpen={isModalOpen}
        onRequestClose={toggleModal}
        contentLabel="My dialog"
      >
        <PageHeader
          title="A game of 16 questionsbeat Carnac!"
          subtitle="AI Jimmy is really good at identifying the object in images that he
            has seen before. Select (check) which of the following images you
            think Jimmy remembers. You will be playing against Carnac and who
            ever gets more correct, wins!"
        />
        <button
          onClick={toggleModal}
          style={{
            position: "absolute",
            top: 5,
            right: 5,
            border: 0,
            background: "none",
          }}
        >
          <CheckButton color="black" isSelected={true} />
        </button>
      </Modal>
      <Fab
        alwaysShowTitle={true}
        mainButtonStyles={{
          backgroundColor: "slategray",
        }}
        icon=". . ."
      >
        <Action text="Back to Game Menu" onClick={returnToMenu}>
          <Emoji symbol="0x21A9" />
        </Action>
      </Fab>
    </>
  )
})

const GameView = () => {
  return (
    <GameContextProvider>
      <Router>
        <Switch>
          <Route path="/20q/learn">
            <TwentyQuestionsPractice />
          </Route>
          <Route path="/20q/easy">
            <TwentyQuestions gameMode="easy" />
          </Route>
          <Route path="/20q/hard">
            <TwentyQuestions gameMode="hard" />
          </Route>
          <Route path="/match/learn">
            <Match gameMode="learn" />
          </Route>
          <Route path="/match/easy">
            <Match gameMode="easy" />
          </Route>
          <Route path="/match/hard">
            <Match gameMode="hard" />
          </Route>
          <Route path="/">
            <Menu />
            <Story />
          </Route>
        </Switch>
        <GameModal />
      </Router>
    </GameContextProvider>
  )
}

export default GameView
