const MembershipInferenceAttack = ({ status }) => (
  <div style={stepOpacityStyle(status)}>
    <ScrollyHeaderSection>
      Membership Inference Attack (MIA)
    </ScrollyHeaderSection>
    <ScrollContainer>
      <ScrollyContentSection style={{ flexBasis: "50%" }}>
        <div style={{ margin: 20, position: "sticky", top: 10 }}>
          <MiaFlowChart status={status} />
        </div>
      </ScrollyContentSection>
      <ScrollyContentSection style={{ flexBasis: "50%" }}>
        <p>
          In the membership inference attack, we want to answer the question of
          whether a specific piece of data was in the training data set for the
          machine learning model. Here, one does not necessarily need to have
          access to the original data set, though we have the ability to query
          the model with input data and obtain the outputs.
        </p>
        <p>
          There are several operational scenarios in which we need to answer
          this question. First, we may have a deployed model that has been
          supporting an application for some time. If the data point was used to
          train that model, it could make the model noncompliant and we would
          have to terminate its use. Furthermore, any actions previously taken
          or data previously collected based upon the model could also be
          noncompliant. Right now, legal precedence says to throw out the model
          and any evidence that may have been collected with it.
        </p>
        <p>
          Second, you may have a trained a model using a small amount of data
          that was collected with NTM and as such, it has a higher
          classification given its relationship to TTP, sources and methods.
          Given that you do not own the data, it is feasible that others will
          have access to the data as well. For example, 2nd and 3rd party
          partners where this model will be deployed into. If that sensitive
          data can be readily identified from the model output, it puts NTM at
          risk.
        </p>
      </ScrollyContentSection>
    </ScrollContainer>
  </div>
)

const ModelInversionAttack = ({ status }) => (
  <div style={stepOpacityStyle(status)}>
    <ScrollyHeaderSection>Model Inversion Attack</ScrollyHeaderSection>
    <div style={{ display: "flex", justifyContent: "space-between" }}>
      <img src="story-img/Shallow.png" id="shallow" alt="" />
      <div style={{ margin: "0 50px" }}>
        There is no consensus in the machine learning community about what data
        features DNNs learn while training, which makes it tough to interpret
        them. However, the work Zeiler and Fergus in the field of visual
        interpretations of DNNs is a useful starting point. Their method
        transposes DNNs to find which image patches are responsible for
        certain neural activations. With the associated results, they argued the
        shallow layers identify rough features such as edges and colors in the
        training data set and the deeper layers combine these features into
        higher level shapes and object abstractions.
      </div>
      <img src="story-img/Deep.png" id="deep" alt="" />
    </div>
    <VerticalSpacer height={300} />
    <p>
      Here are some class prototypes from the Zeieler and Fergus paper. These
      images are not real and would never be found in any data set that was
      generated via imagery collection. However, can you understand what the DNN
      is looking for in each output class?
    </p>
    <ImageClasses />
    {/* <img src="story-img/ClassPrototypes.png" id="class prototypes" alt="" /> */}
    <VerticalSpacer height={300} />
    <p>
      In the model inversion attack, the output nodes of a DNN are used to
      reconstruct a prototype represenation of each class. The optimization
      process used to accomplish this is very similar to the one used in the
      training of the DNN and the technqiue used by Zeiler and Fergus. In the
      case of image classification, a DNN is a function, f, that maps
      high-dimensional input image features of height H, width W, and D
      channels, to a probability vector with dimensions, d, matching the number
      of possible categories. Model inversion seeks to find an image such that
      the output probability for that class is maximized, while the other
      possible outputs are minimized.
    </p>
    <p>
      Stochastic gradient ascent can be used with an associated cost function to
      iteratively transform a proposed image to the local maxima of the
      differentiable function, f. This is very similar to how weight updates
      occur in neural network training, where the inputs and outputs are
      constant. In this case, the weights and output class are held constant and
      the input is updated.
    </p>
    <p>
      These class prototypes are images represent the extracted features that
      allow the network to best differentiate amongst the classes.
    </p>
    <InputOutput />
    <p>
      However, anyone can perform model inversion against any model. Below we
      include an example of audio inversion. Can you hear what each class is
      looking for?
    </p>
    <ScrollContainer>
      <ScrollyContentSection style={{ flexBasis: "100%" }}>
        <ReverseEngineeringDataPrototypes width="60vw" />
      </ScrollyContentSection>
    </ScrollContainer>
  </div>
)

const ReverseEngineeringDataPrototypes = (props) => (
  <RowFlexContainer>
    <ImageAnimation
      images={[
        "story-img/match_game_cifar_robust_images_deer_threshold_1.png",
        "story-img/match_game_cifar_robust_images_deer_threshold_16.png",
        "story-img/match_game_cifar_robust_images_deer_threshold_32.png",
        "story-img/match_game_cifar_robust_images_deer_threshold_64.png",
        "story-img/match_game_cifar_robust_images_deer_threshold_128.png",
        "story-img/match_game_cifar_robust_images_deer_threshold_256.png",
      ]}
      info={
        <>
          <h3>Reverse Engineering Data Prototypes</h3>
          <p style={{ fontSize: ".85rem", color: "lightslategray" }}>
            The model inversion attack seeks to reconstruct representations of
            the training data in order to glean information about sensitive
            data-collection tools, techniques and tradecraft. For computer
            vision models, inversion can produce image representations for every
            class label in the model. The interactive figure below demonstrates
            real images formed during an inversion of the deer class. The
            inversion process is an optimization problem similar to that used to
            train the model in the first place. Here, the image inversion is
            randomly initialized and a new image is saved at each stage. In the
            early stages of inversion optimization, these images typically
            remain gray and not visually interpretable and thus we refer to
            these generated images "gray data". However, as the inversion
            process continues, these images can have significant elements that
            are human interpretable.
          </p>
        </>
      }
      {...props}
    />
  </RowFlexContainer>
)

const TwentyQuestions = ({ status }) => (
  <div style={stepOpacityStyle(status)}>
    <ScrollyHeaderSection>A game of 20 Questions...</ScrollyHeaderSection>
    <ScrollContainer>
      <ScrollyContentSection style={{ flexBasis: "60%" }}>
        <div style={{ position: "sticky", top: 10 }}>
          <p>
            If DNNs did not leak any information about it's training set, there
            would be no way to answer this question better than randomly
            guessing 'yes' or 'no'.
          </p>
          <p>
            Thus, perhaps the easiest strategy for an attacker is to predict
            'no' everytime the model gets the class prediction wrong. This
            strategy is based upon the notion that since the DNN is good at this
            task, if it gets the prediction wrong, then most likely it has not
            seen the image before.
          </p>
          <p>
            This is problematic for models that are severely overfit, where
            there is a large gap between the accuracy of training images and new
            images. As such, this is known as the <b> 'Gap Attack' </b> and it
            will have results better than random guessing by an amount related
            to the size of the gap.
          </p>
          <p>
            However, even for models that are not overfit and only output the
            predicted label, there is a way to perform a successful membership
            attack. By adding noise to the input, it is possible to trick the
            model into a wrong classification. To see this, add noise to the
            images on the right. One image was in the training data set and the
            other was not. But by adding enough noise to the images, a
            successful misclassification will occur. See if you can determine a
            noise level that gives you strong confidence about whether the image
            was used in the training or not. This is known as the{" "}
            <b>'Label-Only Attack' </b> since it can be performed even when the
            model's output is nothing more than the class label.
          </p>
        </div>
      </ScrollyContentSection>
      <ScrollyContentSection style={{ flexBasis: "40%" }}>
        <ImageNoise />
      </ScrollyContentSection>
    </ScrollContainer>
  </div>
)

const DotMatrix = ({ status }) => (
  <div style={stepOpacityStyle(status)}>
    <ScrollyHeaderSection>Label-Only Attack Threshold</ScrollyHeaderSection>
    <ScrollContainer>
      <ScrollyContentSection style={{ flexBasis: "55%" }}>
        <ExperimentThresholdCharts
          nodeSize={13}
          defaultThreshold={4.5}
          bottomLabel="Distance"
          sliderLabel="Threshold"
          // thresholdPercent={status && status.progress ? status.progress : null}
        />
      </ScrollyContentSection>
      <ScrollyContentSection style={{ flexBasis: "50%" }}>
        <div style={{ position: "sticky", top: 10 }}>
          <VerticalSpacer height={75} />
          <p>
            For images that the model has been trained on, the amount of noise
            needed to fool the model is typically higher. In other words, for
            images not in the training, it is easier to obtain an incorrect
            label. By using your own data, you can determine the noise level
            that will give optimal attack results. This can be used in the
            attack of other target models.
          </p>
        </div>
      </ScrollyContentSection>
    </ScrollContainer>
  </div>
)

