const Debugger = ({ viewState, hide }) => {
  if (hide) return null
  const progressPercent = (x) => (x ? (x * 100).toFixed() : 0)
  return (
    <div
      style={{
        position: "fixed",
        top: 50,
        left: 50,
        zIndex: 999999999999,
        padding: 5,
        border: "1px solid black",
        backgroundColor: "pink",
      }}
    >
      <div>
        <DebuggerTitle>View:</DebuggerTitle>
        {viewState.currentView || "None"}
        <br />
        <DebuggerTitle>Scroll:</DebuggerTitle>
        {progressPercent(
          (viewState[viewState.currentView] || { progress: "" }).progress
        )}
        %
      </div>
    </div>
  )
}

const Story = ({ debugScrolling = false }) => {
  const [currentViewState, setCurrentViewState] = useState({})
  const [viewState, setViewState] = useState({})

  const onStepEnter = ({ data }) =>
    setCurrentViewState({ view: data, visible: true })

  const onStepExit = ({ data }) =>
    setCurrentViewState({ view: data, visible: false })

  const onStepProgress = ({ data, progress }) =>
    setCurrentViewState({ view: data, visible: true, progress })

  useEffect(
    () =>
      currentViewState.view
        ? setViewState({
            ...viewState,
            [currentViewState.view]: {
              ...viewState[currentViewState.view],
              ...currentViewState,
            },
            currentView: currentViewState.view,
          })
        : null, // eslint-disable-next-line react-hooks/exhaustive-deps
    [currentViewState]
  )

  return (
    <>
      <ScrollerContainer>
        <Scroller>
          <Scrollama
            onStepEnter={onStepEnter}
            onStepExit={onStepExit}
            progress
            onStepProgress={onStepProgress}
            offset={0.4}
            debug={debugScrolling}
          >
            <Step data="weakness">
              <StepContainer>
                <WeaknessWithAI status={viewState["weakness"]} />
              </StepContainer>
            </Step>
            <Step data="puppies-or-food">
              <StepContainer>
                <PuppiesOrFood status={viewState["puppies-or-food"]} />
              </StepContainer>
            </Step>
            <Step data="mia">
              <StepContainer>
                <MembershipInferenceAttack status={viewState["mia"]} />
              </StepContainer>
            </Step>
            <Step data="20q">
              <StepContainer>
                <TwentyQuestions status={viewState["20q"]} />
              </StepContainer>
            </Step>
            <Step data="dot-matrix">
              <StepContainer>
                <DotMatrix status={viewState["dot-matrix"]} />
              </StepContainer>
            </Step>
            <Step data="model-inversion-attack">
              <StepContainer>
                <ModelInversionAttack
                  status={viewState["model-inversion-attack"]}
                />
              </StepContainer>
            </Step>
          </Scrollama>
        </Scroller>
        <Debugger hide={!debugScrolling} viewState={viewState} />
      </ScrollerContainer>
    </>
  )
}

export default Story
