import { useState, useEffect, useRef, useCallback } from "react"
import styled, { keyframes } from "styled-components"
import Xarrow from "react-xarrows"
import TargetModelDiagram from "./TargetModel"
import { Emoji } from "../game/Util"

const fadeIn = keyframes`
  from { opacity: 0; }
  to { opacity: 1; }
`

const Input = styled.div`
  height: 100px;
  width: 100px;
  background-color: gray;
  margin: 10px;
  margin-left: 35px;
  font-size: 1.2rem;
  &::before {
    content: "x\\208${({ index }) => index}";
    position: relative;
    left: -65%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: inherit;
  }
`

const Inputs = ({ count, images }) => {
  const inputs = new Array(count).fill(0)
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
      }}
    >
      {inputs.map((_, i) => (
        <Input
          key={`input-${i + 1}`}
          index={i + 1}
          id={`input-${i + 1}`}
          style={{
            backgroundImage: images[i] ? `url(story-img/${images[i]})` : null,
          }}
        />
      ))}
    </div>
  )
}

const StyledOutputDiagram = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  animation: ${fadeIn} 0.1s linear;
`

const OutputDiagram = ({ width = "100%", height = "100%", index }) => {
  const boxStyle = (heightOffset) => ({
    height: 10 + (heightOffset || 0),
    rx: 1,
    y: 40 - (heightOffset || 0),
  })
  const ellipseStyle = {
    stroke: "rgb(63,108,175)",
    strokeWidth: 4,
    fill: "none",
  }
  return (
    <StyledOutputDiagram
      style={{ width, height, position: "absolute" }}
      id={`output-${index}`}
    >
      {/* <svg viewBox={`0 0 ${width} ${height}`}> */}
      <svg viewBox={`0 0 100 85`}>
        <rect fill="rgb(176,0,4)" style={boxStyle()} x={2} width={15} />
        <rect fill="#eee" style={boxStyle()} x={20} width={22} />
        <rect fill="black" x={21} y={44} height={2} width={2} />
        <rect fill="black" x={25} y={44} height={2} width={2} />
        <rect fill="black" x={29} y={44} height={2} width={2} />
        <rect
          fill="black"
          style={boxStyle((index - 1) * 5)}
          x={43}
          width={15}
        />
        <rect fill="rgb(255,226,129)" style={boxStyle()} x={61} width={19} />
        <rect fill="rgb(130,201,63)" style={boxStyle()} x={80} width={18} />
        <ellipse cx="50" cy="42" rx="15" ry="40" style={ellipseStyle} />
        <text x="50" y="72" textAnchor="middle" style={{ font: "14px arial" }}>
          ?
        </text>
      </svg>
    </StyledOutputDiagram>
  )
}

const Output = styled.div`
  position: relative;
  height: 100px;
  min-height: 100px;
  margin-top: -25px;
  padding: 0.25em;
  width: 120px;
  border: 3px solid #333;
  background-color: white;
  font-size: 1.3rem;
  &::before {
    /* content: "\\1D453(x\\208${({ index }) => index})"; */
    position: relative;
    left: -80%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: inherit;
  }
  &:nth-child(odd) {
    left: 150px;
    margin-left: 30px;
  }
  &:nth-child(even) {
    left: 35px;
  }
`

const Outputs = ({ count }) => {
  const inputs = new Array(count).fill(0)
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        maxHeight: 200,
        paddingTop: 25,
        justifyContent: "space-evenly",
      }}
    >
      {inputs.map((_, i) => (
        <Output index={i + 1} key={`output-${i + 1}`}>
          <OutputDiagram index={i + 1} />
        </Output>
      ))}
    </div>
  )
}

const lineColors = [
  "",
  "black",
  "rgb(130,120,66)",
  "rgb(252,0,26)",
  "rgb(58,105,173)",
]
// Can't figure out how to send unicode to LeaderLine labels
const HACK_lineLabels = ["", "(x)", "(x)", "(x)", "(x)"]

const LineLabel = ({ index }) => (
  <div style={{ color: lineColors[index], marginTop: 15 }}>
    {HACK_lineLabels[index]}
  </div>
)

const inputImages = [
  null,
  "input-output-input-2.png",
  "input-output-input-3.png",
  "input-output-input-4.png",
]

const ActionButton = styled.button`
  background-color: rgb(63, 108, 175);
  color: white;
  border: none;
  padding: 8px;
  border-radius: 5px;
  font-size: 16pt;
  outline: none;
  vertical-align: top;
  height: 42px;
  &:disabled {
    pointer-events: none;
    color: gray;
  }
  &:hover {
    text-shadow: 1px 1px 1px black;
  }
  &:nth-child(odd) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-right: 2px solid gray;
  }
  &:nth-child(even) {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
`

const Buttons = ({ onClick, actionDisabled }) => {
  return (
    <div style={{ textAlign: "center", marginBottom: 15 }}>
      <ActionButton onClick={() => onClick(1)} disabled={actionDisabled}>
        <Emoji symbol="0x203A" /> Step Inversion
      </ActionButton>
      <ActionButton onClick={() => onClick()} title="Reset">
        <Emoji symbol="0x21BB" />
      </ActionButton>
    </div>
  )
}

const InputOutput = () => {
  const [inputCount, setInputCount] = useState(0)
  const linesOld = useRef([])
  const [lines, setLines] = useState([])

  useEffect(() => {
    setTimeout(() => setInputCount(1), 1000) // HACK, can't figure out how to wait on rendering lines
  }, [])

  const drawLines = useCallback(() => {
    if (inputCount > 0) {
      const _lines = []

      for (let i = 1; i <= inputCount; i++) {
        const lineOptions = {
          color: lineColors[i],
          size: 2.5,
          animateDrawing: "0.5s",
          strokeWidth: 2,
          path: "straight",
        }
        _lines.push({
          ...lineOptions,
          start: `input-${i}`,
          end: "target-model",
        })
        _lines.push({
          ...lineOptions,
          startAnchor: { position: "auto", offset: { rightness: -54 } },
          start: "target-model",
          end: `output-${i}`,
          label: { end: <LineLabel index={i} /> },
        })
      }
      setLines(_lines)
    }
  }, [inputCount])

  useEffect(() => {
    drawLines()
  }, [drawLines])

  const updateInputCount = (delta) =>
    setInputCount((prev) => {
      if (delta === null || delta === undefined) setInputCount(1)
      else {
        const updated = prev + delta
        setInputCount(Math.min(Math.max(1, updated), 4))
      }
    })

  return (
    <div style={{ marginBottom: 300 }}>
      <Buttons onClick={updateInputCount} actionDisabled={inputCount === 4} />
      <div
        style={{
          display: "flex",
          justifyContent: "space-around",
          alignItems: "center",
          minWidth: 900,
          maxWidth: "90%",
        }}
        ref={linesOld}
      >
        <Inputs count={inputCount} images={inputImages} />
        <TargetModelDiagram
          style={{ width: 275 }}
          id="target-model"
          additionalShapes={[
            <circle
              key="circled-output-shape"
              cx="100"
              cy="50"
              r="12"
              stroke="rgb(63,108,175)"
              strokeWidth={2}
              fill="none"
            />,
          ]}
        />
        <Outputs count={inputCount} />
        {lines.map((x) => (
          <Xarrow key={`xarrow-${x.start}-${x.end}`} {...x} />
        ))}
      </div>
      {/* <img src="story-img/SGD4.png" id="sgd4" alt="" /> */}
    </div>
  )
}

export default InputOutput
