import PropTypes from "prop-types"
import styled, { keyframes } from "styled-components"
import "./Game.css"
import { isDefined } from "../../util/data"
import { Range } from "react-range"
import InformationTooltip from "./InformationTooltip"

const CenteredOuter = styled.div`
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  height: 100%;
`
const CenteredInner = styled.div`
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  width: 100%;
`

export const Centered = ({ children }) => (
  <CenteredOuter>
    <CenteredInner>{children}</CenteredInner>
  </CenteredOuter>
)

const ToggleButtonCheckBoxWrapper = styled.div`
  position: relative;
  background-color: lightgray;
  padding: 5px;
  border-radius: 4px;
`

const CheckButton = styled.button`
  background: ${({ checked, backgroundColor }) =>
    checked ? backgroundColor : "gray"};
  padding: 5px;
  color: ${({ checked, color }) => (checked ? color : "#444")};
  outline: none;
  border: none;
  border-radius: ${({ left }) => (left ? "4px 0 0 4px" : "0 4px 4px 0")};
  transition: text-shadow 0.25s ease;
  &:hover {
    cursor: pointer;
    text-shadow: ${({ checked }) =>
      checked ? "1px 1px 1px black;" : "1px 1px 1px lightgray;"};
  }
`

export const ToggleButton = ({
  trueLabel = "YES",
  falseLabel = "NO",
  trueBgColor,
  falseBgColor,
  onToggle,
  defaultValue,
}) => {
  return (
    <ToggleButtonCheckBoxWrapper>
      <CheckButton
        checked={isDefined(defaultValue) && defaultValue}
        backgroundColor={trueBgColor}
        onClick={() => onToggle(true)}
        left={true}
      >
        {trueLabel}
      </CheckButton>
      <CheckButton
        checked={isDefined(defaultValue) && !defaultValue}
        backgroundColor={falseBgColor}
        onClick={() => onToggle(false)}
        right={true}
      >
        {falseLabel}
      </CheckButton>
    </ToggleButtonCheckBoxWrapper>
  )
}

ToggleButton.propTypes = {
  onToggle: PropTypes.func.isRequired,
  trueLabel: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  falseLabel: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
}

export const GameButton = styled.button`
  border: 1px solid lightgray;
  text-transform: uppercase;
  font-family: Futura, sans-serif;
  font-size: 1.4rem;
  padding: 5px;
  border-bottom: 7px solid grey;
  color: darkslategray;
  border-radius: 10px 10px 2px 2px;
  outline: none;
  cursor: pointer;
  &:disabled {
    cursor: default;
    color: lightgray;
  }
  &:hover:enabled {
    text-shadow: 1px 1px 1px #aaa;
  }
`

export const Emoji = ({ className, style, label, symbol }) => {
  const symbols = Array.isArray(symbol) ? symbol : [symbol]
  return (
    <span className={className} style={style} role="img" aria-label={label}>
      {symbols.map((x) => String.fromCodePoint(x))}
    </span>
  )
}

export const Sidecar = styled.div`
  background-color: white;
`

const StyledRangeSlider = styled.div`
  margin: 10px 10px 0 10px;
  outline: none;
`

export const ControlLabel = styled.div`
  ${({ position = "top" }) =>
    position === "top" ? `margin-bottom: 10px;` : `margin-top: 10px;`}
  font-size: 0.9rem;
  text-align: center;
`

const RangeTrack = styled.div`
  height: 6px;
  width: 100%;
  background-color: #ccc;
  border-radius: 3px;
`

const RangeHandle = styled.div`
  height: 18px;
  width: 18px;
  color: white;
  background-color: #999;
  border: 1px solid #555;
  font-size: 0.7rem;
  text-align: center;
  line-height: 1.7;
  outline: none;
  border-radius: 4px;
`

export const RangeSlider = ({
  value,
  min,
  max,
  onChange,
  style,
  step = 1,
  label = null,
  labelPosition = "top",
  handleStyle,
}) => {
  const _label = label ? (
    <ControlLabel position={labelPosition}>{label}</ControlLabel>
  ) : null
  return (
    <StyledRangeSlider style={style}>
      {_label && labelPosition === "top" ? _label : null}
      <Range
        step={step}
        min={min}
        max={max}
        values={[value]}
        onChange={(values) => {
          const [_value] = values
          if (_value !== value) {
            onChange(_value)
          }
        }}
        renderTrack={({ props, children }) => (
          <RangeTrack {...props}>{children}</RangeTrack>
        )}
        renderThumb={({ props }) => (
          <RangeHandle {...props} style={handleStyle}>
            {value}
          </RangeHandle>
        )}
      />
      {_label && labelPosition === "bottom" ? (
        <div style={{ marginTop: 10 }}>{_label}</div>
      ) : (
        <div style={{ height: 7 }}></div>
      )}
    </StyledRangeSlider>
  )
}

const StyledGameTitle = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  text-shadow: 2px 2px 2px slategray;
  text-align: center;
`

export const GameTitle = ({ children, information, style }) => (
  <StyledGameTitle style={style}>
    {children}
    {information ? (
      <InformationTooltip>{information}</InformationTooltip>
    ) : null}
  </StyledGameTitle>
)

const fadeIn = keyframes`
  from { opacity: 0; }
  to { opacity: 1; }
`

const StyledAnswerSvg = styled.div`
  display: inline-block;
  animation: ${fadeIn} 0.1s linear;
`

export const WrongAnswerX = ({
  width = 75,
  height = 100,
  strokeWidth = 8,
  color = "red",
}) => {
  const style = {
    stroke: color,
    fill: "none",
    strokeWidth,
    rx: 1,
    strokeLinecap: "round",
  }
  const cornerPadding = strokeWidth * 2.5
  return (
    <StyledAnswerSvg style={{ width, height }}>
      <svg viewBox={`0 0 ${width} ${height}`}>
        <rect
          style={style}
          x={strokeWidth}
          y={strokeWidth}
          width={width - strokeWidth * 2}
          height={height - strokeWidth * 2}
        />
        <line
          x1={cornerPadding}
          y1={cornerPadding}
          x2={width - cornerPadding}
          y2={height - cornerPadding}
          style={style}
        />
        <line
          x1={width - cornerPadding}
          y1={cornerPadding}
          x2={cornerPadding}
          y2={height - cornerPadding}
          style={style}
        />
      </svg>
    </StyledAnswerSvg>
  )
}

export const RightAnswerCheck = ({
  width = 50,
  height = 50,
  strokeWidth = 6,
  color = "green",
}) => {
  const style = {
    stroke: color,
    fill: "none",
    strokeWidth,
    strokeLinejoin: "round",
  }
  const checkPoints = [
    `${width * 0.3},${height * 0.5}`,
    `${width * 0.45},${height * 0.65}`,
    `${width * 0.7},${height * 0.32}`,
  ]
  return (
    <StyledAnswerSvg style={{ width, height }}>
      <svg viewBox={`0 0 ${width} ${height}`}>
        <circle
          style={style}
          cx={width / 2}
          cy={height / 2}
          r={width / 2 - 5}
        />
        <polyline style={style} points={checkPoints.join(" ")} />
      </svg>
    </StyledAnswerSvg>
  )
}
