import { useState, useEffect } from "react"
import styled from "styled-components"
import { TagCloud } from "react-tagcloud"

const Model = [
  { img: "story-img/class-arch.png", label: "Arch", tagSize: 25 },
  { img: "story-img/class-barn.png", label: "Barn", tagSize: 35 },
  { img: "story-img/class-beach.png", label: "Beach", tagSize: 25 },
  { img: "story-img/class-beetle.png", label: "Beetle", tagSize: 35 },
  { img: "story-img/class-broom.png", label: "Broom", tagSize: 25 },
  { img: "story-img/class-candle.png", label: "Candle", tagSize: 35 },
  { img: "story-img/class-lamp.png", label: "Lamp", tagSize: 25 },
  { img: "story-img/class-phone.png", label: "Phone", tagSize: 35 },
  { img: "story-img/class-pool-table.png", label: "Pool Table", tagSize: 25 },
  { img: "story-img/class-ship.png", label: "Ship", tagSize: 35 },
  { img: "story-img/class-skunk.png", label: "Skunk", tagSize: 35 },
  { img: "story-img/class-toaster.png", label: "Toaster", tagSize: 25 },
]

const data = Model.map((x, i) => ({ value: x.label, count: x.tagSize }))

const StyledImg = styled.img`
  border-radius: 5px;
  margin: 0 2px 0 2px;
  display: inline-block;
`

const Image = ({
  details: { img, label },
  imageWidth,
  onSelect = () => null,
}) => {
  const [hovered, setHovered] = useState(false)
  const onMouseOver = (label) => {
    setHovered(label ? true : false)
    onSelect(label)
  }

  const style = () => ({
    width: imageWidth,
    height: "auto",
    boxShadow: hovered ? "1px 1px 5px gray" : "none",
  })

  return (
    <StyledImg
      onMouseOver={() => onMouseOver(label)}
      onMouseOut={() => onMouseOver()}
      src={img}
      alt={label}
      style={style()}
    />
  )
}

const Images = ({ images, width = 640, imageWidth = 130, onSelect }) => {
  return (
    <div style={{ width }}>
      {images.map((x) => (
        <Image
          key={`card-${x.label}`}
          details={x}
          imageWidth={imageWidth}
          onSelect={onSelect}
        />
      ))}
    </div>
  )
}

const Tag = styled.div`
  display: inline-block;
  margin: 3px 10px;
  padding: 0 4px;
  border: 3px solid ${({ color }) => color};
  border-radius: 3px;
  font-size: ${({ size }) => size}pt;
  transition: border 0.2s linear;
`

const customRenderer = (tag, size, color) => {
  return (
    <Tag key={tag.value} size={size} color={color}>
      {tag.value}
    </Tag>
  )
}
const ImageClasses = ({ model = Model }) => {
  const [selected, setSelected] = useState()
  const [images, setImages] = useState([])

  useEffect(() => {
    setImages(model) // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <Images images={images} onSelect={(label) => setSelected(label)} />
      <div style={{ display: "flex", alignContent: "center" }}>
        <TagCloud
          style={{ width: 400, textAlign: "center" }}
          tags={data}
          minSize={25}
          maxSize={35}
          renderer={(tag) =>
            customRenderer(
              tag,
              tag.count,
              tag.value === selected ? "orange" : "rgba(0,0,0,0)"
            )
          }
        />
      </div>
    </div>
  )
}

export default ImageClasses
