const colors = ["0,0,255", "255,0,0", "0,255,0", "255,0,255"]

const defaultDataSet = {
  borderWidth: 1,
}

const dataSet = (index) => {
  const color = colors[index]
  const backgroundColor = `rgba(${color},0.4)`
  const borderColor = `rgba(${color},0.7)`
  return {
    ...defaultDataSet,
    backgroundColor,
    borderColor,
  }
}

const selectionData = (data) =>
  Object.keys(data.MI_class_results || {}).reduce(
    (all, x) => ({ ...all, [x]: data.MI_class_results[x].mi_auc }),
    {}
  )

export const configurationKeys = (configurations) =>
  Object.keys(configurations).filter(
    (x) => typeof configurations[x] === "object"
  )

const fetchConfigurationData = async (configuration) => {
  const key = (x) => `${x.flavor}_${x.dataset}_${x.architecture}_${x.train}`
  const filepath = (name) => `./data/${name}.json`
  const filekey = key(configuration)
  let results
  try {
    const response = await fetch(filepath(filekey))
    results = await response.json()
  } catch (e) {
    console.log(`No data file available for configuration ${filekey}`)
    const response = await fetch(filepath("missing_data"))
    results = await response.json()
  }
  return [filekey, results]
}

export const configurationData = async (configuration) => {
  const [key, json] = await fetchConfigurationData(configuration)
  return {
    key,
    data: selectionData(json),
  }
}

export const chartData = (selections) => {
  const selectionKeys = configurationKeys(selections)
  const hasData = selectionKeys.reduce(
    (all, x) => all && "data" in selections[x],
    true
  )
  if (hasData) {
    const labels = [
      ...new Set(
        selectionKeys.reduce(
          (all, x) => [...all, ...Object.keys(selections[x].data || {})],
          []
        )
      ),
    ].sort((a, b) => parseInt(a) > parseInt(b))
    const keyData = selectionKeys.reduce(
      (all, x) => ({
        ...all,
        [selections[x].key]: {
          data: selections[x].data,
          label: `MI AUC (${selections[x].flavor}/${selections[x].dataset}/${selections[x].architecture}/${selections[x].train})`.toUpperCase(),
        },
      }),
      {}
    )
    return {
      labels,
      datasets: Object.keys(keyData).map((x, i) => {
        return {
          ...dataSet(i),
          label: keyData[x].label,
          data: labels.map((y) => keyData[x].data[y]),
        }
      }),
    }
  } else return {}
}

const sample = (items, size) =>
  items
    .map((x) => ({ x, r: Math.random() }))
    .sort((a, b) => a.r - b.r)
    .map((a) => a.x)
    .slice(0, size || items.length)

export const PRACTICE_ROUND_IMAGE_COUNT = 5
export const GAME_IMAGE_COUNT = 12

const sanitizeJson = (obj) => {
  const stringToBooleanOrOriginal = (p) =>
    p && (p === "true" || p === "false") ? p.toLowerCase() === "true" : p
  return Object.keys(obj).reduce(
    (all, x) => ({
      ...all,
      [x]: stringToBooleanOrOriginal(obj[x]),
    }),
    {}
  )
}

export const cleanObject = (x) =>
  Object.keys(x || {})
    .filter((key) => x[key])
    .reduce((all, key) => ({ ...all, [key]: x[key] }), {})

// const PRACTICE_SETS = [1, 2]
// const PRACTICE_SETS = [1, 2, 3, 4, 5]

const allGroups = (data) =>
  data.reduce((all, x) => [...new Set([...all, ...(x.groups || [])])], [])

const practiceSet = (images, groupNumber) => {
  // setIndex = isDef(setIndex)
  //   ? setIndex
  //   : PRACTICE_SETS[Math.floor(Math.random() * PRACTICE_SETS.length)]
  return images.filter((x) => x.groups && x.groups.includes(groupNumber))
}

export const gameData = async ({
  path = "./data/game-data.json",
  snippetId = null,
} = {}) => {
  const fetchPath = snippetId
    ? `https://gitlab-int.ctc.com/api/v4/snippets/${snippetId}/raw`
    : path
  const allOptions = await fetch(fetchPath)
  const data = await allOptions.json()
  const correctData = data.map(sanitizeJson)
  return {
    practiceOptions: allGroups(correctData).reduce(
      (all, x) => ({ ...all, [x]: practiceSet(correctData, x) }),
      {}
    ),
    gameOptions: sample(correctData, GAME_IMAGE_COUNT),
  }
}

